import React, { Fragment, useState } from "react";
import { CgDetailsMore } from "react-icons/cg";
import { ImCheckboxChecked, ImCheckboxUnchecked } from "react-icons/im";
import { IoMdTrash } from "react-icons/io";
import { MdEdit } from "react-icons/md";
import { QuestionAnswersStore } from "../../actions";
import { questionTypesStoreInterface } from "../../actions/questionTypes";
import EditQuestion from "../../containers/QuestionBank/AddQuestion/UploadQuestion/EditQuestion/EditQuestion";
import { QuestionInterface } from "../../interfaces/question";
import Modal, { ModalSize, Themes } from "../Modal/Modal";
import QuestionPreview from "../QuestionPreview/QuestionPreview";

interface QuestionItemProps {
  question: QuestionInterface;
  callBack: (question: QuestionInterface) => void;
  status: boolean;
  questionTypes: questionTypesStoreInterface;
  select: boolean;
  number: number;
  questionsList: QuestionInterface[];
}

const QuestionItem = (props: QuestionItemProps) => {
  const [editMode, setEditMode] = useState<boolean>(false);
  const [selectedQuestion, setSelectedQuestion] =
    useState<QuestionInterface | null>(null);
  const unSelectQuestion = () => {
    setEditMode(false);
  };
  const answerQuestionFn = (answer: QuestionAnswersStore) => {};
  return (
    <Fragment>
      {
        editMode === false ? (
          <div
            onClick={() =>
              props.select === true && props.callBack(props.question)
            }
            className={`bg-gray-50 cursor-pointer rounded border border-gray-200 px-3 py-2 text-gray-700 hover:text-blue-600 flex flex-row items-center justify-between gap-4 ${
              props.select === true ? "hover:bg-blue-50" : "hover:bg-white"
            } hover:border-blue-600 mb-3`}
          >
            <div className="flex flex-row items-center justify-between w-full gap-2 md:gap-10">
              <div className="flex flex-row items-center gap-3 w-full truncate">
                <div>
                  {props.select === false ? (
                    <div className="font-bold text-blue-600">
                      ({props.number})
                    </div>
                  ) : props.status === true ? (
                    <ImCheckboxChecked className="text-xl text-blue-600" />
                  ) : (
                    <ImCheckboxUnchecked className="text-xl text-blue-600" />
                  )}
                </div>
                <div className="flex flex-col">
                  <span className="text-xs text-blue-600 font-bold w-max">
                    {props.questionTypes.questionTypes &&
                      props.questionTypes.questionTypes.find(
                        (itm) => itm.id && itm.id === props.question.type
                      )?.title}
                  </span>
                  <span className="font-bold">
                    {props.question.question[0].title}
                  </span>
                  <div
                    className="w-full text-xs"
                    dangerouslySetInnerHTML={{
                      __html: props.question.question[0].description,
                    }}
                  ></div>
                </div>
              </div>
              {props.select === false && (
                <div className="flex flex-row items-center gap-2">
                  <div
                    onClick={() => setSelectedQuestion(props.question)}
                    className="bg-gray-50 hover:bg-gray-200 rounded-full px-1 py-1 cursor-pointer"
                  >
                    <CgDetailsMore className="text-3xl text-gray-700" />
                  </div>
                  <div
                    onClick={() => setEditMode(true)}
                    className="bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 cursor-pointer"
                  >
                    <MdEdit className="text-3xl text-blue-700" />
                  </div>
                  <div
                    onClick={() =>
                      props.select === false && props.callBack(props.question)
                    }
                    className="bg-gray-100 hover:bg-red-100 rounded-full px-1 py-1 cursor-pointer"
                  >
                    <IoMdTrash className="text-3xl text-yellow-700" />
                  </div>
                </div>
              )}
            </div>
          </div>
        ) : (
          // <Modal
          //   title=""
          //   backDrop={true}
          //   theme={Themes.default}
          //   close={() => setEditMode(false)}
          //   backDropClose={true}
          //   widthSizeClass={ModalSize.extraLarge}
          //   displayClose={false}
          //   padding={{ title: false }}
          // >
          <div className="pt-0 fixed top-0 z-50 left-0 h-full overflow-scroll animate__animated animate__zoomIn shadow-xl bg-white w-full">
            <EditQuestion
              selectedQuestion={props.question}
              questionsList={props.questionsList}
              unSelectQuestion={unSelectQuestion}
            />
          </div>
        )
        // </Modal>
      }

      {selectedQuestion !== null && (
        <Modal
          title={
            <div className="text-2xl font-extrabold text-blue-600">
              Question preview
            </div>
          }
          backDrop={true}
          theme={Themes.default}
          close={() => setSelectedQuestion(null)}
          backDropClose={true}
          widthSizeClass={ModalSize.extraLarge}
          displayClose={true}
          padding={{ title: true }}
        >
          <QuestionPreview
            questionAnswers={[]}
            answerQuestionFn={answerQuestionFn}
            selectedQuestion={selectedQuestion}
            setSelectedQuestion={setSelectedQuestion}
            preview={true}
            submitQuestionAnswer={false}
          />
        </Modal>
      )}
    </Fragment>
  );
};

export default QuestionItem;
