import { ExamProctoringRules } from "../actions/candidate_exam";

export enum QuestionNumberFormat {
  ABC = "ABC",
  abc = "abc",
  NUMBER = "NUMBER",
  I = "I",
}

export const returnQuestionNumbering = (numbering: QuestionNumberFormat) => {
  switch (numbering) {
    case QuestionNumberFormat.ABC:
      return "A,B,C";
    case QuestionNumberFormat.abc:
      return "a,b,c";
    case QuestionNumberFormat.I:
      return "i, ii, iii";
    case QuestionNumberFormat.NUMBER:
      return "1,2,3";
  }
};

export const EXAM_QUESTIONS_NUMBERING = [
  {
    key: QuestionNumberFormat.NUMBER,
    value: returnQuestionNumbering(QuestionNumberFormat.NUMBER),
  },
  {
    key: QuestionNumberFormat.ABC,
    value: returnQuestionNumbering(QuestionNumberFormat.ABC),
  },
  {
    key: QuestionNumberFormat.abc,
    value: returnQuestionNumbering(QuestionNumberFormat.abc),
  },
  {
    key: QuestionNumberFormat.I,
    value: returnQuestionNumbering(QuestionNumberFormat.I),
  },
];

