import React from "react";
import { applicationPeriodsStoreInterface } from "../../actions/application_periods";
import { courseStoreInterface } from "../../actions/course";
import { ExamItemResponse } from "../../interfaces/prepare_exam";
import { PrepareActionTypes } from "./PrepareExam";

export enum PrepareExamStepsEnum {
  Step1 = "Step1",
  Step2 = "Step2",
}

interface PrepareExamStepProps {
  currentStep: PrepareExamStepsEnum;
  setStep: (step: PrepareExamStepsEnum) => void;
  course: courseStoreInterface;
  periods: applicationPeriodsStoreInterface;
  setSelectCourse: (status: boolean) => void;
  setSelectPeriod: (status: boolean) => void;
  stepStatus: (step: PrepareExamStepsEnum) => boolean;
  preparedExams?: ExamItemResponse[] | null;
  actionType: PrepareActionTypes;
}

const PrepareExamSteps = (props: PrepareExamStepProps) => {
  const Steps: {
    step: PrepareExamStepsEnum;
    title: string;
    description: string;
    status: boolean;
  }[] = [
    {
      step: PrepareExamStepsEnum.Step1,
      title:
        props.actionType === PrepareActionTypes.Prepare
          ? "Prepare questions"
          : "Edit questions",
      description: "Choose questions for the exam",
      status: false,
    },
    {
      step: PrepareExamStepsEnum.Step2,
      title: "Exam details",
      description: "Fill all exam details",
      status: false,
    },
  ];
  return (
    <div className="mt-3 rounded px-2 flex flex-col gap-3 bg-white py-2">
      <div className="flex flex-row items center justify-between">
        <div className="flex flex-row items-end w-full">
          <div className="flex flex-col px-2 w-full">
            <span className="text-blue-600 text-3xl font-bold">
              {props.actionType === PrepareActionTypes.Prepare
                ? props.course.selected_course === null
                  ? "Prepare exam"
                  : "Exam: " + props.course.selected_course.course_name
                : props.course.selected_course === null
                ? "Edit the exam"
                : "Exam: " + props.course.selected_course.course_name}
            </span>
            {props.periods.selected_period !== null && (
              <span
                className={`text-sm ${
                  props.periods.selected_period.is_active === true
                    ? "text-gray-600"
                    : "text-yellow-600"
                }`}
              >
                Session: {props.periods.selected_period.session_plan_name}
              </span>
            )}
            <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-2 lg:gap-6 w-full">
              <div className="flex flex-row items-center gap-6">
                {Steps.map((item, i) => (
                  <div
                    onClick={() => props.setStep(item.step)}
                    key={i + 1}
                    className={`flex flex-row items-center gap-4 my-2 rounded p-2 border ${
                      props.currentStep === item.step
                        ? props.stepStatus(item.step) === true
                          ? "border-blue-300"
                          : "bg-blue-50 border-white"
                        : "cursor-pointer hover:bg-gray-50 border-white"
                    }`}
                  >
                    <div
                      className={`w-10 h-10 rounded-full ${
                        props.stepStatus(item.step) === true
                          ? "bg-blue-600 text-white"
                          : props.currentStep === item.step
                          ? "bg-white text-blue-600 border-2 border-blue-300"
                          : "bg-gray-200"
                      } text-center font-bold text-xl flex items-center justify-center`}
                    >
                      {i + 1}
                    </div>
                    <div className="flex flex-col">
                      <span
                        className={`${
                          props.stepStatus(item.step) === true
                            ? "text-blue-600"
                            : ""
                        } font-bold text-lg`}
                      >
                        {item.title}
                      </span>
                      <span className="text-sm -mt-1 text-gray-500">
                        {item.description}
                      </span>
                    </div>
                  </div>
                ))}
              </div>
              {props.actionType === PrepareActionTypes.Prepare && (
                <div className="flex flex-row items-center gap-2">
                  <div
                    onClick={() => props.setSelectCourse(true)}
                    className={`btn ${
                      props.course.selected_course === null
                        ? "bg-yellow-50 hover:bg-yellow-100 border border-yellow-600 text-yellow-600"
                        : "bg-blue-50 hover:bg-blue-100 text-blue-600"
                    } h-max flex items-center justify-center cursor-pointer py-2`}
                  >
                    {props.course.selected_course === null
                      ? "Choose course"
                      : "Change course"}
                  </div>
                  {(props.periods.selected_period === null ||
                    props.periods.selected_period.is_active === false) && (
                    <div
                      onClick={() => props.setSelectPeriod(true)}
                      className={`btn ${
                        props.periods.selected_period === null
                          ? "bg-yellow-50 hover:bg-yellow-100 border border-yellow-600 text-yellow-600"
                          : "bg-white hover:bg-blue-100 border border-blue-600 text-blue-600"
                      } h-max flex items-center justify-center py-2 cursor-pointer`}
                    >
                      {props.periods.selected_period === null
                        ? "Choose period"
                        : "Change Period"}
                    </div>
                  )}
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PrepareExamSteps;
